.block {
	display: flex;
 }	// remove this

#map {
	height: 400px {
    width: 100%; } }

.wrapper {
	max-width: 1024px;
	margin: 0 auto;
	@include martel; }

.container {
	margin: 0 auto;
	max-width: 725px;
	width: 100%;
	text-align: center;
	position: relative;
	@include r(750) {
		margin: 0 20px; } }

.header {
	margin: 0 auto;
	position: relative;
	height: 126px;
	max-width: 832px;
	.container {
		position: absolute;
		top: 50%;
		transform: translate(0,-50%); }
	&__wrap {
		position: absolute;
		top: 50%;
		height: 126px;
		margin-top: -71px; }
	&__logo {
		float: left; }
	&__nav {
		float: right; } }

.logo {
	font-size: 26px;
	text-decoration: none;
	&__part1 {
		color: #0e1d2b; }
	&__part2 {
		color: #ffa326; } }

.nav {
	font-size: 16px;
	a {
		text-decoration: none;
		color: #0e1d2b;
		@include martel; } }

.hero {
	height: 100vh;
	min-height: 400px;
	background-size: cover;
	background-position: 50% 100%;
	background-repeat: no-repeat;
	display: table;
	width: 100%;
	&__container {
		display: table-row; }
	&__left, &__right {
		width: 50%;
		display: table-cell;
		height: 100vh;
		background-size: cover;
		background-position: 50% 100%;
		background-repeat: no-repeat;
		border: 0; } }
