html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "martel-regular-webfont";
  src: url("../fonts/martel.woff2") format("woff2"), url("../fonts/martel.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "martel-bold-webfont";
  src: url("../fonts/martel-bold.woff2") format("woff2"), url("../fonts/martel-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

#map {
  height: 400px;
  height-width: 100%;
}

.wrapper {
  max-width: 1024px;
  margin: 0 auto;
  font-family: martel, sans-serif;
}

.container {
  margin: 0 auto;
  max-width: 725px;
  width: 100%;
  text-align: center;
  position: relative;
}

@media only screen and (max-width: 750px) {
  .container {
    margin: 0 20px;
  }
}

.header {
  margin: 0 auto;
  position: relative;
  height: 126px;
  max-width: 832px;
}

.header .container {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

.header__wrap {
  position: absolute;
  top: 50%;
  height: 126px;
  margin-top: -71px;
}

.header__logo {
  float: left;
}

.header__nav {
  float: right;
}

.logo {
  font-size: 26px;
  text-decoration: none;
}

.logo__part1 {
  color: #0e1d2b;
}

.logo__part2 {
  color: #ffa326;
}

.nav {
  font-size: 16px;
}

.nav a {
  text-decoration: none;
  color: #0e1d2b;
  font-family: martel, sans-serif;
}

.hero {
  height: 100vh;
  min-height: 400px;
  background-size: cover;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  display: table;
  width: 100%;
}

.hero__container {
  display: table-row;
}

.hero__left, .hero__right {
  width: 50%;
  display: table-cell;
  height: 100vh;
  background-size: cover;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  border: 0;
}
